import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges
} from '@angular/core';

// import { isPropertyChanged } from '../../utils';

@Component({
  selector: 'ta-initial-indicator',
  templateUrl: './initial-indicator.component.html',
  styleUrls: ['./initial-indicator.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class InitialIndicatorComponent implements OnInit, OnChanges {
  @Input() public name: string;

  public initial: string;

  private readonly splitKey = ' ';

  constructor() {}

  public ngOnInit(): void {}

  public ngOnChanges(changes: SimpleChanges): void {
    // if (isPropertyChanged(changes.name)) {
    this.setInitial();
    // }
  }

  private setInitial(): void {
    const nameSplits = this.name.split(this.splitKey);
    this.initial =
      nameSplits.length > 1 ? this.getInitial(nameSplits) : this.name;
  }

  private getInitial(nameSplits: string[]): string {
    return `${this.getWordByIndex(nameSplits, 0)}${this.getWordByIndex(
      nameSplits,
      1
    )}`;
  }

  private getWordByIndex(nameSplits: string[], index: number) {
    return nameSplits[index][0].toLocaleUpperCase();
  }
}
